.main-container
{
    max-width: 1320px;
    margin: 0 auto;
    .menu
    {
        display: flex;
        align-items: center;
        align-self:center;
        justify-content: space-between;
        padding:0px 5px;
        gap:10px;
        .right
        {
            flex: 4;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .menu-item
            {
               
                
                ul
                {
                    display: flex;
                    align-items: center;
                    list-style: none;
                    li
                    {
                        padding: 10px;

                        a
                        {
                            padding: 5px;
                            font-size: 13px;
                            font-weight: bold;
                        }
                    }
                }
                
            }
            .location
            {
                display: flex;
                align-items: center;
                gap: 10px;
                p 
                {
                    font-size: 13px;
                    font-weight: bold;
                }
            }
        }
        .left
        {

            flex: 1;
            display: flex;
            align-items: center;
            color: #fff;
            padding: 8px 2px;
            background-color:#849D6A;
            margin-top: 5px;
            gap: 5px;
            justify-content: space-between;
            .list-right
            {
                display: flex;
                align-items: center;
                gap: 10px;
            }
            img
            {
                margin: 3px;
            }

        }
    }

    .banner
    {
        display: flex;
        padding: 0px 5px;
         margin-top: 5px;
        align-items: center;
        margin-bottom: 50px;
        
        gap: 10px;
        
        
        .left
        {
            flex: 1;
            max-height: 60vh;
           
            
            
        }
        .right
        {
            flex: 4;
            max-height: 60vh;
            
           
        }
    }

    .title-category
    {
        display: flex;
        align-items: center;
        padding: 5px;
        justify-content: space-between;
        h2 
        {
            font-size: 20px;
            font-weight: bold;
           
            
        }
        a
        {
            font-size: 12px;
            border-bottom: 1px solid #000;
            cursor: pointer;
        }
    }


    .timer
    {
        margin-top: 20px;
        display: block;
        position: relative;
        
       

        div
        {
            position: absolute;
            right: 100px;
            top: -15px;
            z-index: 20;
            background-color: #fff;
            width: 260px;
            height: 30px;
            display: flex;
            margin: 0px 3px;
            align-items: center;
            justify-content: space-between;

            span
            {
                margin-left: 5px;
                background-color: #B04D50;
                border-radius: 20px;
                padding: 0px 20px;
                color: #fff;
                font-weight: bold;
            }

            h2 
            {
                font-weight: bold;
                margin-right: 5px;
            }
        }
        
    }
}
@media  (max-width: 995px)
{
    .banner
    {
        padding-left: 30px;
    }
    .menu
    {
        .right
        {
            flex-direction: row-reverse;
            .menu-item
            {
                display: none;
            }
        }
    }
     
    
}
@media  (max-width: 694px)
{
    .left
    {
        display: none;
    }
   
}