.category-cards
{
    padding: 5px;
    display: grid;
    gap: 30px;
    align-items: center;
    justify-content: center;
    grid-template-columns: repeat(4,1fr);

    .category-card
    {
        display: grid;
        align-items: center;
        grid-template-columns: repeat(2,1fr);
        padding: 20px 10px;
        padding-right: 25px;
        border: 1px solid #EBEBEB;
        border-radius: 5px;
        position: relative;
        img 
          {
            position: absolute;
            bottom: 16px;
            left: 12px;
            width: 140px;
          }
          .text
          {
            p 
            {
              padding-top: 4px;
                font-size: 13px;
                font-weight: bold;
                margin-bottom: 6px;
            }
            ul
            {
              margin-bottom: 5px;
             
             li {
                font-size: 13px;
                padding: 3px;
                
                
             }
            }
            
        
            a
            {
                margin-top: 6px;
                color: #B04D50;
                font-size: 13px;
                font-weight: bold;
                border-bottom: 1px solid #B04D50;
                cursor: pointer;

            }
          }

    }

   
}
.bullet
{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    gap: 10px;

    span 
    {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #41544A;
    }
    .empty-bullet
    {
        width: 14px;
        height: 14px;
        border: 1px solid #41544A;
        border-radius: 50%;
        background-color: #fff;

    }
   
}

@media (max-width:1122px)
{
    .category-cards
    {
        grid-template-columns: repeat(2,1fr);
        .category-card
        {
            justify-content: center;
        }
    }
}

@media (max-width:595px)
{
    .category-cards
    {
        grid-template-columns: repeat(1,1fr);
        .category-card
        {
            justify-content: center;
        }
    }
}