*
{
    margin: 0;
    box-sizing: border-box;
    padding: 0;
    outline: 0;
}



.sidebar
{
    
    
    
    ul
    {
        list-style: none;
        border: 1px solid #eee;
        
        a
        {
            display: flex;
            align-items: center;
            padding-top: 8px;
            padding-bottom: 8px;
            padding-right: 10px;
            border-bottom: 1px solid #eee;
            img 
            {
                width: 20px;
                padding-left: 5px;
            }
            li
            {
                display: flex;
                gap: 5px;
                align-items: center;
                font-size: 15px;
               
            }
           
        }
    }
}




@media  (max-width: 810px)
{
    .dropdown
    {
        display: none;
    }
    .left
    {
        p
        {
            display: none;
        }
    }
   
}
@media  (max-width: 694px)
{
    .sidebar
    {
        display: none;
    }
   
}