.slider-product
{
    display: flex;
    align-items: center;
    justify-content: center;
    
    .swiperstyle
    {
        border: 1px solid red;
        

        .card-body
        {
            display: grid;
            padding: 30px 0px;
            align-items: center;
            justify-items: center;
           
            .product-img
            {
                position: relative;
                .save
                {
                    position: absolute;
                    right: 20px;
                    top: 20px;
                }
            }
             
            ul 
            {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    list-style: none;
            }
            p
            {
                padding-top: 35px;
                color: #B04D50;
                font-weight: bold;
            } 
            span 
            {
                width: 150px;
                border-radius: 5px;
                height: 3px;
                margin-top: 15px;
                background-color: #B04D50;
            }           
        }
    }
}
