.choose
{
  padding: 10px;
  margin-top: 35px;
  margin-bottom: 35px;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 10px;
    .card-choose
    {
        width: 100%;
        height: 60px;
        display: flex;
        align-items: center;
        gap: 30px;

        h3 
        {
            font-size: 16px;
            font-weight: bold;
        }
        p 
        {
            font-size: 14px;
        }
        img 
        {
            width: 65px;
        }
    }
}

@media (max-width:1122px)
{
    .choose
    {
        grid-template-columns: repeat(2,1fr);
        .card-choose
        {
            justify-content: center;
        }
    }
}
@media (max-width:595px)
{
    .choose
    {
        grid-template-columns: repeat(1,1fr);
        .card-choose
        {
            justify-content: center;
        }
    }
}