

.banner-img
{
    width: 100%;
    
   .banner
   {
    background-image: url('../../public/image/slide.png');
    width: 100%;
    height:60vh;
    background-size: cover;
    position: relative;
    .arrow-icon
    {
        display: flex;
        width: 97%;
        position: absolute;
        top: 55px;
        align-items: center;
        justify-content: space-between;
    }
    .text
    {
        position: absolute;
        top: 75px;
        right: 85px;
        p
        {
            font-size: 14px;
            font-weight: 900;
            color: #41544A;
        }
        h2 
        {
            color: #41544A;
            font-size: 60px;
            font-weight: bold;
        }
        h3 
        {
            color: #849D6A;
            font-size: 60px;
            font-weight:100;
        }
        h5
        {
            font-size: 16px;
            font-weight: bold;
            color: #41544A;
        }
        button
        {
            background-color: #41544A;
            border: none;
            padding: 12px 16px;
            font-weight: bold;
            font-size: 13px;
            color: #fff;
            margin-top: 5px;
            border-radius: 5px;
            

        }
    }
   }
   
}

@media (max-width:1024px)
{
    .banner
    {
        .right
        {
            .text
            {
                h2
                {
                    font-size: 40px;
                }
                h3
                {
                    font-size: 40px;
                }
                h5 
                {
                    font-size: 10px;
                }
                button
                {
                    padding: 6px 8px;
                }
            }
        }
    }
}
@media (max-width:816px)
{
    .banner
    {
        .right
        {
            .text
            {
                right: 63px;

                h2
                {
                    font-size: 28px;
                }
                h3
                {
                    font-size: 28px;
                }
                h5 
                {
                    font-size: 10px;
                    margin-top: 5px;
                    margin-bottom: 5px;
                }
            }
        }
    }

}

