.cards
{
    margin-bottom: 20px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: auto;
    gap: 30px;

        .card-image
        {
            width: 100%;
            display: block;
        }
    
}
@media (max-width:595px)
{
    .cards
    {
        grid-template-columns: repeat(1,1fr); 
        padding: 20px;
    }
}