*{
    direction: rtl;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    outline: 0;
}

.container
{
    border-bottom: 1px solid #eee;
    padding: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

  .right
  {
    flex: 2;
    display: flex;
    align-items: center;
    gap: 20px;
    .logo
    {
       
        img
        {
            width: 100px;
            height: 50px;
        }
    }

    .search
    {
       
        display: flex;
        align-items: center;
        justify-content: flex-end;
        height: 40px;
        width:65%;
        border: 1px solid #eee;
        
        select
        {
            border: none;
            height: 100%;
            padding: 5px 10px;

        }
        input
        {
            border: none;
            padding: 5px 10px;
            width: 100%;
            height: 100%;
            border-right: 1px solid #eee;
        }
        span
        {
            display: flex;
            align-items: center;
            padding: 5px 10px;
            height: 100%;
            background-color:#849D6A ;
            img
            {
                width: 20px;
            }
        }
    }
  }

    .account
    {
        
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: space-evenly;
        width: 155px;
        height: 31px;
        .account-position
        {
            img
            {
                width: 20px;
            }
            position: relative;
            .alarm
                {
                    position: absolute;
                    top: -7px;
                    left: -6px;
                    padding: 3px 5px;
                    font-size: 10px;
                    border-radius: 50%;
                    background-color: #86BC42;
                }
        }
      
    }
}

@media  (max-width: 740px)
{
 
    .right
    {
        .search
        {
            select
            {
                display: none;
            }
        }
    }
  
}

@media  (max-width: 576px)
{
    .search
    {
        visibility: hidden;
    }
}